* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #1e1e1e;
  font-family: "Open Sans", sans-serif;
}

.content-wrapper {
  margin: auto;
  width: 90%;
  max-width: 768px;
}

header {
  min-height: 100vh;
  /*
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;*/
}

header .content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 2rem 0;
}

header #site-title {
  color: rgba(255, 255, 255, 0.7);
  /*text-shadow: 2px 3px 4px rgba(0,0,0,0.75);*/
  font-size: 3.5rem;
  font-weight: 800;
  letter-spacing: 0.65rem;
  text-transform: uppercase;
  text-align: center;
}

.animation-element.fade-in {
  opacity: 0;
  transform: translateY(2rem);
  transition: opacity ease-in 2s, transform cubic-bezier(0.43, 0.04, 0, 1) 2.5s;
}

.animation-element.fade-in-faster {
  opacity: 0;
  transform: translateY(2rem);
  transition: opacity ease-in 2s, transform cubic-bezier(0.43, 0.04, 0, 1) 2s;
}

.animation-element.fade-in-faster.in-view,
.animation-element.fade-in.in-view {
  opacity: 1;
  transform: translateY(0);
}

header .content-wrapper p small {
  text-align: center;
  text-shadow: 2px 3px 2px rgba(0, 0, 0, 0.2);
  color: rgba(200, 200, 200, 0.75);
}

#cover-letter .content-wrapper {
  margin-top: 4rem;
  padding: 1rem 0;
}

#cover-letter .content-wrapper p {
  font-size: 1.25rem;
  color: #c8c8c8;
  text-align: center;
  line-height: 3rem;
}

#computer-skills {
  margin: auto;
  max-width: 500px;
  width: 100%;
  padding: 3rem 0;
  font-family: "Hack", monospace;
}

#computer-skills .computer-skill {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 2rem 0;
}

main .animation-element.fade-up {
  transform: translateY(2rem);
  opacity: 0;
  transition: transform ease-in 1s, opacity ease-in 0.7s;
}

main .animation-element.fade-up.in-view {
  transform: translateY(0);
  opacity: 1;
}

main .animation-element.fill-up {
  width: 0;
  transition: width cubic-bezier(0, 0, 0, 1) 1s;
}

main .animation-element.fill-up.in-view {
  width: 100%;
}

#computer-skills .computer-skill .text-container {
  width: 100%;
  text-align: left;
  margin: 1rem 0 0.25rem;
}

#computer-skills .computer-skill .text-container p {
  color: #e6e6e6;
  text-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  font-size: 1.25rem;
  font-weight: bold;
}

#computer-skills .computer-skill .proficiency-container {
  width: 90%;
  border-radius: 1rem;
  border-left: 2px solid rgba(230, 230, 230, 0.75);
  border-right: 2px solid rgba(230, 230, 230, 0.75);
}

#computer-skills .computer-skill .proficiency-container .proficiency .proficiency-colours {
  height: 0.5rem;
  border-radius: 1rem;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.2);
  opacity: 0.75;
}

#skills {
  padding: 3rem 0;
}

#skills .content-wrapper {
  max-width: 600px;
}

#skills .text-container {
  margin: 2rem 0;
  display: flex;
}

#skills .text-container p {
  width: 100%;
  color: #c8c8c8;
  font-style: italic;
}

#skills .text-container:nth-child(2n) {
  justify-content: flex-end;
}

#skills .text-container:nth-child(2n) p {
  text-align: right;
}

#experience .content-wrapper .experience-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 1rem 0;
  margin-bottom: 4rem;
}

#experience .content-wrapper .experience-card:first-child {
  margin-top: 4rem;
}

#experience .content-wrapper .experience-card h1 {
  color: #f0f0f0;
}

#experience .content-wrapper .experience-card h1,
#experience .content-wrapper .experience-card p:not(:last-child) {
  margin-bottom: 0.25rem;
}

#experience .content-wrapper .experience-card p {
  color: #c8c8c8;
}

#experience .content-wrapper .experience-card h2 {
  margin-bottom: 1rem;
  color: #e6e6e6;
}

footer {
  margin-top: 8rem;
  background-color: #282828;
}

footer .content-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

footer .content-wrapper #social-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

footer .content-wrapper #social-links a:not(:last-child) {
  margin-right: 1rem;
}

footer .content-wrapper #social-links a .footer-icon {
  opacity: 0.8;
  width: 2rem;
  height: 2rem;
}

footer .content-wrapper #back-to-top {
  text-decoration: none;
  transition: color ease-in-out 0.1s;
}

footer .content-wrapper #back-to-top:hover {
  color: rgba(230, 62, 123, 0.75);
}

footer .content-wrapper #back-to-top,
footer .content-wrapper #copyright {
  margin: 1rem 0;
  color: #f0f0f0;
}
